.mdw-image-gallery-3d {
  --disabled-section-padding-top: 70px;
  --disabled-section-padding-bottom: 50px;
  position: relative;
  overflow: hidden;
  height: 100vh;
}
.mdw-image-gallery-3d .mdw-image-gallery-inner {
  transform-style: preserve-3d;
  perspective: 750px;
  backface-visibility: hidden;
  height: 100%;
  position: relative;
}
.mdw-image-gallery-3d .mdw-image-gallery-inner > .e-con {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate3d(-50%, -50%, 0px);
  transition: all 0.5s ease;
  opacity: 0;
}
.mdw-image-gallery-3d .mdw-image-gallery-inner > .e-con img {
  width: 250px;
  height: 350px;
  object-fit: cover;
  border-radius: 12px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.3);
}
.mdw-image-gallery-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  opacity: 0.6;
}
.mdw-image-gallery-bg img {
  position: absolute;
  left: 0;
  top: 0;
  filter: blur(50px);
  transform: scale(1.125);
  max-width: unset;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.sticky {
  position: fixed;
  top: 0;
}
.sticky-end {
  position: absolute;
  bottom: 0;
  top: auto !important;
}
